import Paper from 'material-ui/Paper';
import TextField from 'material-ui/TextField';
import RaisedButton from  'material-ui/RaisedButton';
import React from 'react';


const paperStyle = {
    height: 300,
    width: 500,
    margin: 10,
    textAlign: 'center',
    display: 'inline-block'
};

const LoginPaper = () => (
    <Paper style={paperStyle} zDepth={3}>
        <TextField
            hintText="Nhập email hoặc tên tài khoản"
            floatingLabelText="Email hoặc tên tài khoản"
        /><br />
        <TextField
            hintText="Nhập mật khẩu"
            floatingLabelText="Mật khẩu"
            type="password"
        /><br />

        <RaisedButton label="Đăng nhập" primary={true} style={{backgroundColor: '#FF9800'}} />

    </Paper>
);

export default LoginPaper


